import React, { Component } from 'react';
import './App.css';
import Service from './Service';
import moment from 'moment';

class App extends Component {
  constructor() {
    super();
    this.state = {
      resource: [],
      projectOptions: [],
      resourceBooking: [],
      resourceTypes: [],
      type: 0,
      priceFrom: 0.0,
      priceTo: 0.0,
      parent: [],
    }
    this.service = new Service();
    this.filterData = [];
  }

  componentDidMount() {
    const projectData = this.service.getData("com.axelor.apps.project.db.Project").then(res => res.json());
    const resourceData = this.service.getData("com.axelor.apps.project.db.Resource").then(res => res.json());
    const resourceBooking = this.service.getData("com.axelor.apps.project.db.ResourceBooking").then(res => res.json());
    const resourceTypes = this.service.getData("com.axelor.apps.project.db.ResourceType").then(res => res.json());

    Promise.all([projectData, resourceData, resourceBooking, resourceTypes])
      .then(([projectRes, resourceRes, resourceBookingRes, resourceTypesRes]) => {
        this.setState({ projectOptions: projectRes.data, resource: resourceRes.data, resourceBooking: resourceBookingRes.data, resourceTypes: resourceTypesRes.data }, () => this.schedulerUpdate())
      });
  }

  schedulerUpdate() {
    const { scheduler, dhtmlx } = window;
    // const imageURLFront = '/axelor-erp/ws/rest/com.axelor.meta.db.MetaFile/';
    const imageURLFront = '../../ws/rest/com.axelor.meta.db.MetaFile/';
    const imageURLBack = '/content/download';
    let i = 0;

    //scheduler config
    scheduler.xy.scale_height = 70;
    scheduler.config.active_link_view = "timeline";
    scheduler.locale.labels.dhx_cal_today_button = "Today";
    scheduler.locale.labels.two_week_timeline_tab = "Week 2";
    scheduler.locale.labels.week_timeline_tab = "Week 1";
    scheduler.locale.labels.month_timeline_tab = "Month";
    scheduler.locale.labels.timeline_tab = "Timeline";
    scheduler.locale.labels.section_custom = "Section";

    scheduler.config.details_on_create = true;
    scheduler.config.details_on_dblclick = true;

    scheduler.config.xml_date = "%Y-%m-%d %H:%i";
    scheduler.config.hour_date = "%h:%i %a";
    scheduler.config.limit_time_select = true;

    scheduler.config.first_hour = 0;
    scheduler.config.last_hour = 22;
    scheduler.config.time_step = 60;
    scheduler.config.select = false;

    scheduler.config.serverLists = {};
    scheduler.serverList("sections", this.state.resource);
    let resources = this.state.resource;

    function formatDate(date) {
      return moment(date).format("YYYY-MM-DD HH:mm")
    }

    let resourceBooking = this.state.resourceBooking;
    if (resourceBooking && resourceBooking !== null) {
      for (i = 0; i < resourceBooking.length; i++) {
        let resourceBook = resourceBooking[i];
        resourceBook.start_date = formatDate(resourceBook.fromDate);
        resourceBook.end_date = formatDate(resourceBook.toDate);
        resourceBook.text = resourceBook.name;
        resourceBook.section_id = resourceBook.resource.id;
      }
    }

    let projectOptions = this.state.projectOptions;
    if (projectOptions && projectOptions !== null) {
      for (i = 0; i < projectOptions.length; i++) {
        let project = projectOptions[i];
        project.key = project.id;
        project.label = project.name;
      }
    }

    //Custom Timeline
    scheduler.config.serverLists["timeline"] = "sections";
    scheduler.createTimelineView({
      name: "timeline",
      x_unit: "hour",
      x_date: "%h:%i%a",
      x_step: 2,
      x_size: 12,
      x_start: 0,
      x_length: 12,
      y_unit: scheduler.serverList("sections"),
      y_property: "section_id",
      render: "tree",
      folder_events_available: false,
      event_dy: 33,
      dx: 200,
      dy: 40,
      folder_dy: 114,
      section_autoheight: false,
      fit_events: true,
      show_unassigned: true,
      resize_events: false,
      label_style: "left: 38px"
    });

    //Week timeline
    scheduler.config.serverLists["week_timeline"] = "sections";
    scheduler.createTimelineView({
      fit_events: true,
      name: "week_timeline",
      render: "tree",
      folder_events_available: false,
      x_unit: "day",
      x_date: "%M %j",
      x_step: 1,
      x_size: 7,
      x_length: 7,
      event_dy: 33,
      dx: 200,
      dy: 40,
      folder_dy: 114,
      full_event__dy: true,
      y_unit: scheduler.serverList("sections"),
      y_property: "section_id",
      section_autoheight: false,
      resize_events: false,
    });

    //week starts from todays date
    scheduler.date.week_start = function (date) {
      return this.date_part(this.add(date, 0, "day"));
    }

    //two week timeline
    scheduler.config.serverLists["two_week_timeline"] = "sections";
    scheduler.createTimelineView({
      fit_events: true,
      name: "two_week_timeline",
      render: "tree",
      resize_events: false,
      folder_events_available: false,
      x_unit: "day",
      x_date: "%M %j",
      x_step: 1,
      x_size: 14,
      x_length: 14,
      event_dy: 33,
      dx: 200,
      dy: 40,
      folder_dy: 114,
      full_event__dy: true,
      y_unit: scheduler.serverList("sections"),
      y_property: "section_id",
      section_autoheight: false,
    });

    //month timeiline
    scheduler.config.serverLists["month_timeline"] = "sections";
    scheduler.createTimelineView({
      fit_events: true,
      resize_events: false,
      name: "month_timeline",
      render: "tree",
      x_unit: "day",
      x_date: "%D %j",
      x_step: 1,
      x_size: 14,
      x_length: 7,
      event_dy: 33,
      dx: 200,
      dy: 40,
      folder_dy: 114,
      full_event__dy: true,
      y_unit: scheduler.serverList("sections"),
      y_property: "section_id",
      section_autoheight: false,
    });

    //month start
    scheduler.date.week_timeline_start = scheduler.date.two_week_timeline_start = scheduler.date.week_start;
    scheduler.date.month_timeline_start = scheduler.date.month_start;

    scheduler.date.add_month_timeline = function (date, inc) {
      return scheduler.date.add(date, inc, "month");
    };

    scheduler.attachEvent("onBeforeViewChange", function (old_mode, old_date, mode, date) {
      if (mode === "month_timeline") {
        let year = date.getFullYear();
        let month = (date.getMonth() + 1);
        let d = new Date(year, month, 0);
        let days = d.getDate();
        scheduler.matrix['month_timeline'].x_size = days;
        scheduler.matrix['month_timeline'].x_length = days;
      }
      return true;
    });

    //date format
    let weekDateFormat = scheduler.date.date_to_str("%M %j");

    scheduler.templates.week_timeline_scale_date = function (date) {
      return "<br/>" + weekDateFormat(date);
    };

    scheduler.templates.two_week_timeline_scale_date =
      scheduler.templates.two_week_timeline_scale_date = function (date) {
        return "<br/>" + weekDateFormat(date);
      };

    scheduler.templates.month_timeline_scale_date = function (date) {
      return getShortDayName(date) + "<br/>" + date.getDate();
    };

    function getShortDayName(date) {
      switch (date.getDay()) {
        case 0:
          return "Su";
        case 1:
          return "Mo";
        case 2:
          return "Tu";
        case 3:
          return "We";
        case 4:
          return "Th";
        case 5:
          return "Fr";
        case 6:
          return "Sa";
        default:
          return "";
      }
    }

    //lightbox section fields 
    scheduler.config.lightbox.sections = [
      { map_to: "text", name: "Name", type: "textarea", height: 100, focus: true },
      { map_to: "project_id", name: "Project", type: "select", options: projectOptions },
      { map_to: "section_id", name: "Resource", type: "select", options: scheduler.serverList("SelectOptions") },
      { name: "time", height: 72, type: "calendar_time", map_to: "auto" },
    ];

    //rented hours for tooltip
    scheduler.calculateDuration = function (event) {
      return Math.round((event.end_date - event.start_date) / (1000 * 60 * 60));
    };

    scheduler.templates.event_bar_text = function (start, end, event) {
      let time = scheduler.calculateDuration(event);
      if (scheduler.getState().mode === "month" && is_short_event(event)) {
        if (typeof resources[event.resource_id] === "undefined") {
          event.resource_id = 12;
        }
        return resources[event.resource_id].label + ", " + time + " h.";
      } else {
        return "Rented for " + time + " hours";
      }
    };

    function short_date_template(start, end) {
      return scheduler.templates.event_header(start, end) + ", " + scheduler.templates.day_date(start);
    }

    function long_date_template(start) {
      return scheduler.templates.event_date(start) + " " + scheduler.templates.day_date(start);
    }

    function is_short_event(event) {
      return +scheduler.date.date_part(new Date(event.start_date)) == +scheduler.date.date_part(new Date(event.end_date))
    }

    scheduler.templates.tooltip_text = function (start, end, event) {
      let lines = [];
      let eventObj = scheduler.getSection(event.section_id);
      let duration = scheduler.calculateDuration(event);
      if (eventObj !== null) {
        lines.push("<b>" + eventObj.label + "</b>, for <b>" + duration + "</b> hours");
        if (is_short_event(event)) {
          lines.push("Rented: " + short_date_template(start, end));
        } else {
          lines.push("From: " + long_date_template(start));
          lines.push("To: " + long_date_template(end));
        }
        let price = eventObj.cost;
        lines.push("Price per hour: " + price);
        let sum = (Math.round((duration * price) * 100) / 100);
        lines.push("Total: <b>" + sum + "</b>");
      }
      return lines.join("<br>");
    };

    //event collision
    scheduler.attachEvent("onEventCollision", function (ev, evs) {
      for (i = 0; i < evs.length; i++) {
        if (ev.section_id !== evs[i].section_id) continue;
        dhtmlx.alert('This resource is already rented for this date.')
      }
      return true;
    });

    //define template
    let label_template = function (key, label, obj) {
      if (obj.parent === true) {
        return `
        <div>
          <div style="position:relative;top:10px;margin-right:58px">
            ${
          obj.resourceImage ? `<img style="width:80px;height:48px" src="${imageURLFront}${obj.resourceImage.id}${imageURLBack}" alt="${label}" />` : ''
          }
            <br>
            <div class="resource_brand">${label}</div>
            <div class="resource_price">${obj.cost}</div>
          </div>
          <div class="open_type">
            <img style="width:"11px", height:"11px""  src="content/${obj.open ? 'collapse' : 'expand'}.png" alt="${label}">
          </div>
        </div>`;
      }
      else {
        return obj.name;
      }
    };

    for (i in scheduler.matrix) {
      scheduler.templates[i + '_scale_label'] = label_template;
    }

    scheduler.templates.event_class = function (start, end, event) {
      if (event.shadow_event) return "virtual_event";
    };

    scheduler._split_events = function (evs) {
      let stack = [];
      let map_to = scheduler.matrix[scheduler.getState().mode].y_property;
      for (i = 0; i < evs.length; i++) {
        stack.push(evs[i]);
        let shadow_ev = scheduler._lame_clone(evs[i]);
        let parent = getParentId(evs[i][map_to]);
        shadow_ev[map_to] = parent;
        if (resources[parent]) {
          shadow_ev._sorder = Math.max(evs[i].resource_number * 1 - 1, 0) || 0;
        }
        shadow_ev.shadow_event = true;
        stack.push(shadow_ev);
      }
      return stack;
    };

    //virtual event
    let vis_evs = scheduler.get_visible_events;
    scheduler.get_visible_events = function (only_timed) {
      let evs = vis_evs.apply(this, arguments);
      let timeline = scheduler.matrix[scheduler.getState().mode];
      if (timeline) {
        evs = this._split_events(evs);
      }
      return evs;
    };

    scheduler.attachEvent("onSchedulerResize", function () { scheduler.setCurrentView(); });

    //init scheduler
    scheduler.init('scheduler_here', new Date(), "timeline");
    scheduler.parse(resourceBooking, "json");

    //after init
    scheduler.initresourcesList = function (resources) {
      let parent = [];
      let children = [];
      let lightbox_options = [];
      let search_hash = window.resources = {};
      let parent_hash = window.sections = {};

      //apart children(resource) and parent(resourceModel)
      for (i = 0; i < resources.length; i++) {
        let resource = resources[i];
        if (resource.isModel === true) {
          resource.parent = true;
          resource.children = [];
          resource.label = resource.name;
          resource.key = resource.id;
          parent.push(resource);
        } else {
          if (resource.resourceModel !== null) {
            resource.parent = false;
            resource.label = resource.name;
            resource.key = resource.id;
            children.push(resource);
          }
        }
      }

      //add children to parent
      children.filter(function (o1) {
        return parent.some(function (o2) {
          if (o1.resourceModel === null) {
            search_hash[o1.id] = o1;
            parent_hash[o1.id] = undefined;
          }
          else if (o1.resourceModel.id === o2.id) {
            o2.children.push(o1);
            search_hash[o2.id] = o2;
            parent_hash[o2.id] = undefined;
            search_hash[o1.id] = o2;
            parent_hash[o1.id] = o2.id;
          }
        });
      })

      //lightbox options
      for (i = 0; i < children.length; i++) {
        lightbox_options.push({
          key: children[i].id,
          label: children[i].label,
        });
      }

      scheduler.updateCollection(scheduler.config.serverLists["timeline"], parent.slice());
      scheduler.updateCollection("SelectOptions", lightbox_options);
    }

    function getParentId(id) {
      const data = window.sections;
      let parentId;
      if (data !== undefined) {
        parentId = window.sections[id];
      }
      return parentId;
    }

    scheduler.initresourcesList(scheduler.serverList(scheduler.config.serverLists["timeline"]));

    function formatUTC(date) {
      return moment.utc(date);
    }

    const updateEventData = (id, record) => {
      scheduler.getEvent(id).text = record.name;
      scheduler.getEvent(id).project_id = record.project.id;
      scheduler.getEvent(id).section_id = record.resource.id;
      scheduler.getEvent(id).start_date = formatUTC(record.fromDate)._d;
      scheduler.getEvent(id).end_date = formatUTC(record.toDate)._d;
      scheduler.updateEvent(id);
    }

    //On event creation
    scheduler.attachEvent("onEventSave", (id, ev, is_new) => {
      let description = scheduler.formSection('Name').getValue();
      let project = scheduler.formSection('Project').getValue();
      let resourceSection = scheduler.formSection('Resource').getValue();
      let time = scheduler.formSection('time').getValue();
      let duration = Math.round((time.end_date - time.start_date) / (1000 * 60 * 60));
      let eventObj = scheduler.getSection(resourceSection);
      let resourceId = eventObj.id;
      let cost = eventObj.cost;

      const formPayload = {
        name: description,
        project: { id: project },
        cost: cost,
        duration: duration,
        fromDate: time.start_date,
        toDate: time.end_date,
        resource: { id: resourceId },
      }

      if (is_new) {
        this.service.add('com.axelor.apps.project.db.ResourceBooking', formPayload).then(res => {
          res.json().then(result => {
            const { data } = result;
            if (res.status === 200) {
              if (data && data.length) {
                const record = data[0];
                let new_event_id = record.id;
                scheduler.changeEventId(id, new_event_id);
                updateEventData(new_event_id, record);
              }
              console.log('Event is added');
            } else {
              console.log('Something going Wrong');
            }
          })
        });
      } else {
        updateEventData(id, formPayload);
      }
      scheduler.endLightbox(true, document.getElementsByClassName('dhx_cal_light_wide')[0]);
    });

    //lightbox display position
    // scheduler.showCover = function (box) {
    //   if (box) {
    //     box.style.display = "block";
    //     box.style.left = '30%';
    //     box.style.top = "20px";
    //   }
    //   scheduler.show_cover();
    // }

    //event drag & drop 
    scheduler.attachEvent("onEventChanged", (id, ev) => {
      let project = scheduler.formSection('Project').getValue();
      let resourceSection = ev.section_id;
      let duration = Math.round((ev.end_date - ev.start_date) / (1000 * 60 * 60));
      let eventObj = scheduler.getSection(resourceSection);
      let resourceId = eventObj.id;
      let cost = eventObj.cost;

      const formPayload = {
        name: ev.text,
        project: { id: project },
        cost: cost,
        duration: duration,
        fromDate: ev.start_date,
        toDate: ev.end_date,
        resource: { id: resourceId },
      }

      this.service.getId('com.axelor.apps.project.db.ResourceBooking', id).then(res => {
        res.json().then(result => {
          const { data } = result;
          if (data && data.length) {
            formPayload.version = data[0].version;
            this.service.update('com.axelor.apps.project.db.ResourceBooking', formPayload, id).then(res => {
              res.json().then(result => {
                console.log('updated')
                const { data } = result;
                if (data && data.length) {
                  const record = data[0];
                  updateEventData(id, record);
                }
              })
            });
          }
        })
      });
    });

    //delete record
    scheduler.attachEvent("onEventDeleted", (id) => {
      this.service.delete('com.axelor.apps.project.db.ResourceBooking', id).then(res => { console.log('deleted') });
    });
  }

  handleFromChange = (e) => {
    this.setState({ [e.target.name]: e.target.value }, this.handleTypeSelection);
  }

  handleTypeChange = (e) => {
    this.setState({ type: e.target.value }, this.handleTypeSelection);
  }

  checkFilterData(id) {
    let c = 0;
    if (this.filterData !== null) {
      for (let i = 0; i < this.filterData.length; i++) {
        if (this.filterData[i].id === id) {
          c = c + 1;
        }
      }
      return c; //if c 0 push element
    }
  }

  handleReset = () => {
    this.setState({
      priceFrom: 0.0,
      priceTo: 0.0,
      type: 0
    })
  }

  handleTypeSelection = () => {
    const { scheduler } = window;
    let resources = this.state.resource;
    let type = parseInt(this.state.type, 8);
    let priceTo = parseFloat(this.state.priceTo);
    let priceFrom = parseFloat(this.state.priceFrom);
    let c;

    //filtration condition
    if ((type === 0) && (priceFrom === 0 || isNaN(priceFrom)) && (priceTo === 0 || isNaN(priceTo))) {
      resources.forEach(element => {
        if (element.parent === true) {
          c = this.checkFilterData(element.id);
          if (c === 0) {
            this.filterData.push(element);
          }
        }
      })
    }
    else if ((type > 0) && (priceFrom === 0 || isNaN(priceFrom)) && (priceTo === 0 || isNaN(priceTo))) {
      resources.forEach(element => {
        if ((element.resourceType !== null)) {
          if (element.parent === true) {
            if (element.resourceType.id === type) {
              c = this.checkFilterData(element.id);
              if (c === 0) {
                this.filterData.push(element);
              }
            }
          }
        }
      })
    } else if ((priceFrom > 0) && (priceTo === 0 || isNaN(priceTo)) && (type === 0)) {
      resources.forEach(element => {
        if (element.parent === true) {
          if (element.cost >= priceFrom) {
            c = this.checkFilterData(element.id);
            if (c === 0) {
              this.filterData.push(element);
            }
          }
        }
      })
    } else if ((priceTo > 0) && (priceFrom === 0 || isNaN(priceFrom)) && (type === 0)) {
      resources.forEach(element => {
        if (element.parent === true) {
          if (element.cost < priceTo) {
            c = this.checkFilterData(element.id);
            if (c === 0) {
              this.filterData.push(element);
            }
          }
        }
      })
    } else if ((priceFrom > 0) && (priceTo === 0 || isNaN(priceTo)) && (type > 0)) {
      resources.forEach(element => {
        if (element.resourceType !== null) {
          if (element.parent === true) {
            if ((element.cost >= priceFrom) && (element.resourceType.id === type)) {
              c = this.checkFilterData(element.id);
              if (c === 0) {
                this.filterData.push(element);
              }
            }
          }
        }
      })
    } else if ((priceTo > 0) && (priceFrom === 0 || isNaN(priceFrom)) && (type > 0)) {
      resources.forEach(element => {
        if (element.resourceType !== null) {
          if (element.parent === true) {
            if ((element.cost < priceTo) && (element.resourceType.id === type)) {
              c = this.checkFilterData(element.id);
              if (c === 0) {
                this.filterData.push(element);
              }
            }
          }
        }
      })
    }
    else if ((priceTo > 0) && (priceFrom > 0) && (type === 0)) {
      resources.forEach(element => {
        if (element.parent === true) {
          if ((element.cost < priceTo) && (element.cost >= priceFrom)) {
            c = this.checkFilterData(element.id);
            if (c === 0) {
              this.filterData.push(element);
            }
          }
        }
      })
    } else if ((type > 0) && (priceFrom > 0) && (priceTo > 0)) {
      resources.forEach(element => {
        if (element.resourceType !== null) {
          if (element.parent === true) {
            if ((element.cost < priceTo) && (element.cost >= priceFrom) && (element.resourceType.id === type)) {
              c = this.checkFilterData(element.id);
              if (c === 0) {
                this.filterData.push(element);
              }
            }
          }
        }
      })
    }
    scheduler.updateCollection(scheduler.config.serverLists["timeline"], this.filterData.slice());
    this.filterData = [];
  }

  render() {
    return (
      <div className="App">
        <div className="row">
          <div id="search-panel" className='col2'>
            <form>
              <div className="search_form">
                <div className="controls">
                  <div style={{ marginBottom: 20 }}>
                    <label>Type:</label><br />
                    <select id="Type" name="type" onChange={this.handleTypeChange} value={this.state.type} style={{ width: '100%',borderRadius:5, height: "25px", marginTop: 5 }}>
                      <option value={0}>Any</option>
                      {
                        this.state.resourceTypes ?
                          this.state.resourceTypes.map((option) =>
                            <option value={option.id} key={option.id}> {option.name}</option>
                          ) : ''
                      }
                    </select>
                  </div>
                  <div style={{ marginBottom: 20 }}>
                    <label>Price From:</label>
                    <input placeholder='Enter price from'
                      style={{ width: '100%', marginTop: 5 ,borderRadius:5, height:20 }}
                      type='number'
                      name='priceFrom'
                      onChange={(e) => this.handleFromChange(e)}
                      value={this.state.priceFrom} />
                  </div>
                  <div>
                    <label>Price To:</label>
                    <input placeholder='Enter price to'
                      type='number'
                      style={{ width: '100%', marginTop: 5, borderRadius:5, height:20 }}
                      name='priceTo'
                      onChange={(e) => this.handleFromChange(e)}
                      value={this.state.priceTo} />
                  </div>

                  <div className='button-div'>
                    <button className='button' onClick={this.handleReset}>Reset</button>
                  </div>
                </div>
              </div>
            </form>
          </div>
          <div id="scheduler-container" className='col8'>
            <div id="scheduler_here" className="dhx_cal_container" style={{ width: '100%', height: window.innerHeight, overflow: 'hidden', marginRight: 10 }}>
              <div>
                <div className="dhx_cal_navline">
                  <div className="dhx_cal_next_button" />
                  <div className="dhx_cal_prev_button" />
                  <div className="dhx_cal_today_button" role="button">TODAY</div>
                  <div className="dhx_cal_date"></div>
                  <div className="dhx_cal_tab dhx_cal_tab_last" name="month_timeline_tab" />
                  <div className="dhx_cal_tab" name="two_week_timeline_tab" ></div>
                  <div className="dhx_cal_tab" name="week_timeline_tab" />
                  <div className="dhx_cal_tab dhx_cal_tab_first" name="timeline_tab" />
                </div>
              </div>
              <div className="dhx_cal_header" />
              <div className="dhx_cal_data" />
            </div>
          </div>
        </div>
      </div>
    );
  }
}
export default App;


